			fileUpload:function(e) {
					var thisBox = this.$refs['photoUpload'];
					var files = e.target.files;
					var file = files[0];
					var that=this;
					if (!/image.(png|jpg|jpeg)/.test(file.type)) { 
						tipShow('选择的文件类型不是图片'); 
						return; 
					}
					var reader = new FileReader();
					reader.addEventListener("load",function(event){
						var str = this.result;
						var img1 = document.createElement('img');
						var img = new Image();
						img.src = str;
						img.onload = function(){
							var cvs = document.createElement('canvas');
							cvs.width = img.width;
							cvs.height = img.height;
							var ctx = cvs.getContext("2d").drawImage(img, 0, 0);
							var newImageData = cvs.toDataURL(file.type, 0.5);
							var url = "/Mobile/User/uploadimg";
							var data = {type:file.type,str:newImageData};
							that.$refs['pic'].setAttribute('src', newImageData);
							// $.post(url,data,function(k){
							// 	if(k.status==0){
							// 		layer.msg('图片上传失败',{icon:5,time:1000});
							// 		return false;
							// 	}else if(k.status==1){
							// 		$("#pic").attr("src","__UPLOAD__"+k.url);
							// 	}else{
							// 		layer.msg('图片格式不正确',{icon:5,time:1000});
							// 		return false;
							// 	}
							// },'json');
						}        	
					});
					reader.readAsDataURL(file);
				}
		}
	}
